.gameWindowContent > h3 {
  color: aliceblue;
  text-align: center;
}

.gameWindow {
  position: fixed;
  width: 100%;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.grid {
  display: grid;
  justify-content: center;
  gap: 7px;
}

.normal {
  background-color: aliceblue;
}

.snake {
  background-color: black;
}

.apple {
  background-color: crimson;
}
