.msg {
  position: absolute;
  left: 50%;
  top: 10%;
  transform: translate(-50%, -50%);
  background-color: rgb(47, 255, 50);
  padding: 10px;
  border-radius: 10px;
  z-index: 10;
}

/* gameWindow */
.gameWindow.activePage {
  animation: window-grow 3s forwards;
  z-index: 0;
}
.gameWindow.prevPage {
  animation: window-grow 3s forwards reverse;
  position: fixed;
  z-index: 0;
}

/* homeContent */
.homeContent.activePage {
  animation: window-move-left-from-center 3s forwards reverse;
  z-index: 1;

}
.homeContent.prevPage {
  animation: window-move-left-from-center 3s forwards;
  z-index: 1;
}
.homeContent.prevPage ~ .navbar-button {
  visibility: hidden;
  display: none;
}

/* loginContainer i Account */
.loginContainer.activePage {
  animation: window-move-center-from-right 3s forwards;
}
.loginContainer.prevPage {
  animation: window-move-center-from-right 3s forwards reverse;
}


@keyframes window-grow {
  from {
    transform: scale(0.6);
  }
  to {
    transform: scale(1);
  }
}

@keyframes window-move-left-from-center {
  from {
    transform: translate(-50%, -50%);
  }
  to {
    transform: translate(-120vw, -50%);
  }
}

@keyframes window-move-center-from-right {
  from {
    transform: translate(120vw, -50%);
  }
  to {
    transform: translate(-50%, -50%);
  }
}