html {
  height: 100%;
}
body {
  margin:0;
  padding:0;
  font-family: sans-serif;
  background: linear-gradient(rgb(20, 30, 48), rgb(36, 59, 85));
}


.homeContent {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
}

.homeMainBox {
  width: 400px;
  padding: 40px;
  /* background: rgba(0,0,0,.5); */
  background: linear-gradient(#0c131e, #101925);
  box-sizing: border-box;
  box-shadow: 0 15px 25px rgba(0,0,0,.6);
  border-radius: 10px;
}

.homeMainBox h3 {
  color: #fff;
  text-align: center;
}

/* .homeMainBox .ikonsBox {
  position: relative;
} */

.homeMainBox .user-box input {
  width: 100%;
  padding: 10px 0;
  font-size: 16px;
  color: #fff;
  margin-bottom: 30px;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
  background: transparent;
}
.homeMainBox .user-box label {
  position: absolute;
  top:0;
  left: 0;
  padding: 10px 0;
  font-size: 16px;
  color: #fff;
  pointer-events: none;
  transition: .5s;
}

.homeMainBox .user-box input:focus ~ label,
.homeMainBox .user-box input:valid ~ label {
  top: -20px;
  left: 0;
  color: #03e9f4;
  font-size: 12px;
}

.homeMainBox form a {
  position: relative;
  display: inline-block;
  padding: 10px 20px;
  color: #03e9f4;
  font-size: 16px;
  text-decoration: none;
  text-transform: uppercase;
  overflow: hidden;
  transition: .5s;
  margin-top: 40px;
  letter-spacing: 4px
}

.welcomeText {
  color: aliceblue;
}

.boardSizeContainer {
  display: flex;
  justify-content: space-evenly;
}

.boardSize {
  color: aliceblue;
  font-size: 17px;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 15px;
  padding: 5px 10px;
  width: 33%;
  cursor: pointer;
}
.boardSize:hover {
  border: 2px solid white;
}

.playButton {
  display: block;
  background: none;
  border: 2px solid #03e9f4;
  color: aliceblue;
  font-size: 1.2em;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 15px;
  /* border-radius: 10px; */
  cursor: pointer;
}
.playButton:hover {
  transform: scale(1.1);
  background: #03e9f4;
  color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px #03e9f4,
              0 0 25px #03e9f4,
              0 0 50px #03e9f4,
              0 0 100px #03e9f4;
}


.active {
  border: 2px solid orange;
}



/* dopisane po zmianie wygladu */
/* .ikonsBox {
  position:absolute;
  left: calc(50% + 200px);
} */
.ikonsBox {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  overflow: hidden;
}

.ikonsBox > div {
  display: flex;
  color: white;
  border: 2px solid white;
  border-left: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  margin-top: 7px;
  cursor: pointer;
  
  --lenght: calc(-100% + 36px);
  transform: translateX(var(--lenght));
  transition: 1s;
}
.ikonsBox > div:hover {
  /* transform: scaleX(1); */
  /* width: auto; */
  /* display: inline; */
  transform: translateX(0);
}


.ikonsBox > div > span {
  white-space: nowrap;
  line-height: 36px;
  padding: 0 4px;
  /* transform: scaleX(0); */
  /* width: 0; */
  /* display: none; */

}

.ikonsBox > div > img {
  padding: 5px;
}


/* rozwijane menu */

.navbar-button {
  position: absolute;
  top: 30px;
  left: 30px;
  cursor: pointer;
  z-index: 3;
}

.navbar-button > div {
  height: 4px;
  width: 30px;
  border-radius: 20px;
  background-color: white;
  margin-top: 5px;
  transition: 2s;
}

.navbar-button.close > div:nth-child(2) {
  transition: .8s;
  opacity: 0;
}

.navbar-button.close > div {
  margin: 0;
}
.navbar-button.close > div:first-child {
  transition-delay: .8s;
  transform: translateY( 2px) rotate(45deg);
}
.navbar-button.close > div:last-child {
  transition-delay: .8s;
  transform: translateY( -5px) rotate(-45deg);
}

.navbar {
  position:absolute;
  height: 100vh;
  width: 300px;
  background-color: rgba(0,0,0,.7);
  z-index: 2;
  transition: 2s;
}
.navbar.showed {
  transform: translateX(0);
}
.navbar.closed {
  transform: translateX(-300px);
}

/* style dla rzeczy znajdujących się na navbarze */
.navbar > div:first-child {
  margin-top: 20%;
}
.navbar > div {
  color: white;
  display: flex;
  justify-content: center;
  margin: 20px;
  border-bottom: 2px solid white;
  cursor: pointer;
}

.navbar > div > span {
  line-height: 44px;
  padding: 0 10px;
}

.navbar > div > img {
  padding: 6px;
}