/* Wspólne Style */

.menuContainer {
  position: absolute;
  height: 100vh;
  width: 100%;
  top: 0%;
  left: 0%;

  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  background-color: rgba(0, 0, 0, 0.85);
}

.menuContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transform: translate(0, -4vh);

  border-radius: 10px;
}

/* Wspólne dla GameStates */

.gameStatesContent {
  border: 2px solid white;
  align-items: stretch;
  width: auto;
}

.gameStatesContent > h1 {
  /* background-color: indianred; */
  color: white;
  margin: 10px;
  margin-bottom: 0;
  padding: 15px 10px;
}
.gameStatesContent > h2 {
  color: white;
  margin: 20px 10px;
}
.gameStatesContent > hr {
  margin: 10px;
  width: auto;
  border: none;
  background-color: white;
  height: 2px;
}
.gameStatesContent > button {
  color: white;
  background: none;
  border: none;
  font-size: 1.2em;
  margin: 0 10px;
  padding: 10px;
  cursor: pointer;
}
.gameStatesContent > button:last-child {
  margin-bottom: 10px;
}

/* Pause */

.pauseStateContent > h1 {
  background-color: indianred;
}
.pauseStateContent > button:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

/* GameOver */

.red {
  color: crimson;
}

.gameOverStateContent > h1 {
  background-color: crimson;
}
.gameOverStateContent > button:hover {
  background-color: rgba(220, 20, 60, 0.5);
}
