html {
  height: 100%;
}
body {
  margin:0;
  padding:0;
  font-family: sans-serif;
  background: linear-gradient(#141e30, #243b55);
}

.loginContainer {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 400px;
  transform: translate(-50%, -50%);
}
.login-box {
  display: flex;
  flex-direction: column;
  padding: 40px;
  background: linear-gradient(#0c131e, #101925);
  box-sizing: border-box;
  box-shadow: 0 15px 25px rgba(0,0,0,.6);
  border-radius: 10px;
  border-top-left-radius: 0;
}

.login-box h2 {
  margin: 0;
  padding: 0;
  color: #fff;
  text-align: center;
}

.login-box h3.error {
  color: crimson;
  text-align: center;
}

.login-box h3.succes {
  color: green;
  text-align: center;
}

.login-box button {
  background: none;
  border: 2px solid #03e9f4;
  color: aliceblue;
  font-size: 1.2em;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 15px;
  /* border-radius: 10px; */
  cursor: pointer;
}
.login-box button:hover {
  transform: scale(1.1);
  background: #03e9f4;
  color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px #03e9f4,
              0 0 25px #03e9f4,
              0 0 50px #03e9f4,
              0 0 100px #03e9f4;
}

.login-box .user-box {
  position: relative;
}

.login-box .user-box input {
  width: 100%;
  padding: 10px 0;
  font-size: 16px;
  color: #fff;
  margin-bottom: 30px;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
  background: transparent;
}
.login-box .user-box label {
  position: absolute;
  top:0;
  left: 0;
  padding: 10px 0;
  font-size: 16px;
  color: #fff;
  pointer-events: none;
  transition: .5s;
}

.login-box .user-box input:focus ~ label ,
.login-box .user-box input:valid ~ label {
  top: -20px;
  left: 0;
  color: #03e9f4;
  font-size: 12px;
}


.back-to-menu {
  position: absolute;
  left: 100%;
  transform: translate(-50%, -50%);
  background-color: crimson;
  border-radius: 20px;
  padding: 5px;
  cursor: pointer;
  display: flex;
  width: 30px;
  overflow: hidden;
  transition: 2s;
}
.back-to-menu:hover,
.back-to-menu:focus {
  animation: expand-width 2s forwards;
}
.back-to-menu:not(:hover){
  animation: rollup-width 2s forwards;
}

.back-to-menu.didMount {
  width: 135px;
  animation: rollup-width 2s forwards 3s;
}

@keyframes expand-width {
  from {
    width: 30px;
  }
  to {
    width: 135px;
  }
}

@keyframes rollup-width {
  from {
    width: 135px;
  }
  to {
    width: 30px;
  }
}

.back-to-menu > span {
  color: white;
  line-height: 30px;
  white-space: nowrap;
  padding-left: 5px;
}

.back-to-menu > img {
  padding: 3px;
}

.tabs-box {
  color: white;
  display: flex;
  width: 300px;
}

.tabs-box > div {
  width: 150px;
  text-align: center;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  padding: 10px;
  background: #0c131e77;
  cursor: pointer;
}

.tabs-box > div.active-tab {
  background: #0c131e;
}